@import "../abstracts/variables";

@property --myColor1 {
    syntax: '<color>';
    initial-value: #2B2B37;
    inherits: false;
}
  
@property --myColor2 {
    syntax: '<color>';
    initial-value: #2B2B37;
    inherits: false;
}

.footer {
    display: flex;
    flex-direction: row;
    color: white;
    background: #1B1A22;
    margin: 0;
    padding: 0;
    padding-bottom: 10vh;
}


.link_box {
    max-width: 238px;
    padding-left: 2.5rem;
    z-index: 2;
    color: white;
    text-decoration: none;
    cursor: pointer;  
    h2 {
        font-size: 2rem;
        line-height: 3rem;
        margin: 0;
    }

    p {
        font-size: 1rem;
        margin: 0;
    }
}

.box_content {
    padding: 1.5rem 2rem;
    overflow: hidden;
    background: linear-gradient(-45deg, var(--myColor1), var(--myColor2));
    
    border-radius: 5px;
    transition: --myColor1 0.4s, --myColor1 0.4s;
}

.box_content:hover {
    --myColor1: #fff59a9b;
    --myColor2: #ff6d6da2;
    opacity: 1;
}

@media (max-width: 768px) {
    .footer {
        width: 100%;
        flex-direction: column;
        align-items: center;
        align-content: center;
        margin-left: 0;
        padding-bottom: 0;
        a {
            width: 100%;
            padding-left: 0;
        }
        .box_content {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-around;
            margin-bottom: 50px;
        p {
                display: none;
            }
        }
    }
}